<template>
  <div class="article_intro">
    <div class="article-items" v-for="(item,index) in article_intros" :key="item.id">
      <h1 class="title">{{item.title}}</h1>
      <img :src="item.pic_src" alt="">
      <div class="text-area">
        <div class="part-one" v-html="item.intro_html"></div>
        <div class="read-more-bg"></div>
      </div>
      <span class="read-more finger" @click="readMore(item.id)">Read More</span>
      <div v-if="index!==article_intros.length-1" class="hr"></div>
    </div>
    <button @click="loadMore" v-if="article_intros.length!==count">加载更多</button>
    <div v-else class="disabled">
      <i class="fa fa-smile-o"></i>&nbsp;
      暂时没有更多啦
    </div>
  </div>

</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "ArticleIntro",
  data() {
    return {
      page: 1,
      limit: 10
    };
  },
  computed: {
    ...mapGetters(["article_intros","count"])
  },
  methods: {
    loadMore: function() {
      let scrolltop = this.$myFunc.getScrolltop()
      let that = this
      that.page++;
      that.getArticleIntros(that.page, 0, function() {
        that.$myFunc.setScrolltop(scrolltop)
        that.set_loading(false);
      })
    },
    readMore: function(id) {
      this.$router.push(`/article/${id}`);
    },
    getArticleIntros: function(page,get_count,cb){
      let that = this
      let reqData = {
        vm: that,
        vo: {
          page: page,
          limit: that.limit,
          req_arr: JSON.stringify(["id", "title", "pic_src", "intro_html", "pv"]),
          get_count: get_count
        }
      };
      this.set_loading(true);
      that.get_article_introsAsync(reqData).then(cb);
    },
    ...mapMutations(["set_loading","set_first","set_count"]),
    ...mapActions(["get_article_introsAsync", "get_articleAsync"])
  },
  props: {
    s: Object
  },
  created: function() {
    this.set_first(true)
    this.set_count(0)
    let that = this;
    // 首次加载，则发送请求
    if (that.article_intros.length == 0) {
      that.getArticleIntros(1,1,function() {
        that.set_loading(false);
        console.log(that.count)
      })
    }
  }
};
</script>

<style scoped lang="scss">
$my-color: #42b983;
.article_intro {
  max-width: 800px;
  margin: auto;
  padding: 1rem;
  background-color: #fff;
}
.article-items {
  position: relative;
  margin: 3rem 0;
  img {
    max-height: 50vh;
    max-width: 100%;
  }
  .part-one {
    text-align: left;
  }
  >>> p {
    display: block;
  }
  .text-area {
    position: relative;
  }
  .read-more-bg {
    z-index: 10;
    width: 100%;
    height: 6rem;
    line-height: 10rem;
    background-image: linear-gradient(transparent, #fff);
    position: absolute;
    bottom: 0;
  }
}
</style>



