<template>
  <div class="archives">
    <ArticleIntro/>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import ArticleIntro from './subcomponents/ArticleIntro'
export default {
  name: "Archives",
  data() {
    return {
      name: "as"
    };
  },
  components:{
    ArticleIntro
  },
  computed: {
    ...mapGetters(["pets"])
  },
  methods: {
    getArticle:function(url){
      this.$router.push(url);
    },
    ...mapMutations(["increment"])
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



