<template>
  <div class="not-found flex-column">
    <img src="../assets/404.png" alt="">
    <div class="text">
      还有 {{countdown}} 秒回到首页...
    </div>
  </div>
</template>

<script>

export default {
  name: "NotFound",
  data() {
    return {
      countdown: 5
    };
  },
  mounted: function() {
    let that = this

    let time = setInterval(()=>{
      that.countdown--
      if(that.countdown < 1){
        clearInterval(time)
        that.$router.push('/')
      }
    },1000)
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.text {
  color: #42b983;
}
img {
  width: 50%;
  max-width: 256px;
}
</style>



