import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Shop from '@/components/Shop'
import Moments from '@/components/Moments'
import Archives from '@/components/Archives'
import About from '@/components/About'
import Article from '@/components/Article'
import Edit from '@/components/Edit'
import Discover from '@/components/Discover'
import CreateGetIt from '@/components/CreateGetIt'
import User from '@/components/User'
import NotFound from '@/components/NotFound'
import Signin from '@/components/subcomponents/Signin'
import Signup from '@/components/subcomponents/Signup'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/shop',
    name: 'Shop',
    component: Shop
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/discover',
    name: 'Discover',
    component: Discover
  },
  {
    path: '/moments',
    name: 'Moments',
    component: Moments
  },
  {
    path: '/archives',
    name: 'Archives',
    component: Archives
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/user/:id',
    name: 'User',
    component: User
  },
  {
    path: '/article/:id',
    name: 'Article',
    component: Article
  },
  {
    path: '/edit',
    name: 'Edit',
    component: Edit
  },
  {
    path: '/discover',
    name: 'Discover',
    component: Discover
  },
  {
    path: '/signin',
    name: 'Signin',
    component: Signin
  },
  {
    path: '/signup',
    name: 'Signup',
    component: Signup
  },
  {
    path: '/addget',
    name: 'CreateGetIt',
    component: CreateGetIt
  },
  {
    path: '/notfound',
    name: 'NotFound',
    component: NotFound
  },
  {
    path: '*',
    redirect: NotFound
  }
  ]
})



