import Vue from 'vue'
import VueResource from 'vue-resource'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)
Vue.use(VueResource)

const state = {
  count: 0,
  user: null,
  show_loading: false,
  uv: 0,
  first_page: true,
  article_intros: [],
  pets: [],
  article: null,
  products: [],
  moments: [],
  getits: [],
  favors: {
    getit: [],
    product: [],
    article: []
  },
  remember_psw: true,
  super_user: false,
  logged_in: false
}
export default new Vuex.Store({
  state,
  actions,
  mutations,
  getters
})



