<template>
  <div class="shop">
    <img src="../assets/1920-540.png" alt="">
    <div class="out-box">
      <div class="product-box">
        <div class="items tp-box" v-for="item in products" :key="item.id">
          <img :src="item.pic_src" alt="pic of product">
          <div class="ctx-box">
            <span class="price">
              <i class="fa fa-cny"></i> &nbsp; {{item.price}}
            </span>
            <span class="like" @click="controlLike(item.id)">
              <i :class="[{liked:favors.product.includes(item.id)},'finger','fa','fa-heart']" ></i> &nbsp;{{item.like}}
            </span>
            <i class="finger fa fa-shopping-cart"  @click="openNewWindow(item.link)"></i>
          </div>
          <div class="name">{{item.name}}</div>
        </div>
      </div>
    </div>
    <button @click="loadMore" v-if="products.length!==count">加载更多</button>
    <div v-else class="disabled nomore">
      <i class="fa fa-smile-o"></i>&nbsp;
      暂时没有更多啦
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "Shop",
  data() {
    return {
      page: 1,
      limit: 24
    };
  },
  computed: {
    ...mapGetters(["products", "user", "favors", "count"])
  },
  methods: {
    openNewWindow: function(url) {
      window.open(url, "_blank");
    },
    controlLike: function(id) {
      let that = this;
      if (!this.user) {
        this.check_user(this);
        return;
      }
      try {
        let reqData = {
          vm: this,
          vo: {
            user_id: that.user.id,
            favor_type: "product",
            favor_id: id
          }
        };
        if (this.favors.product.includes(id)) {
          // 之前已经点赞，取消赞
          this.delete_favorAsync(reqData).then(() => {
            that.products.forEach(ele => {
              if (ele.id === id) {
                ele.like--;
              }
            });
          });
        } else {
          // 点赞
          this.create_favorAsync(reqData).then(() => {
            that.products.forEach(ele => {
              if (ele.id === id) {
                ele.like++;
              }
            });
          });
        }
      } catch (err) {
        that.$myFunc.toast("操作失败", 1500, "请稍后再试");
      }
    },
    loadMore: function() {
      let scrolltop = this.$myFunc.getScrolltop();
      let that = this;
      that.page++;
      that.getProducts(that.page, 0, function() {
        that.$myFunc.setScrolltop(scrolltop);
        that.set_loading(false);
      });
    },
    getProducts: function(page, get_count, cb,tag="") {
      let that = this;
      let reqData = {
        vm: that,
        vo: {
          page: page,
          limit: that.limit,
          get_count: get_count,
          tag: tag
        }
      };
      that.set_loading(true);
      that.get_productsAsync(reqData).then(cb);
    },
    ...mapMutations(["set_loading", "check_user", "set_first", "set_count"]),
    ...mapActions([
      "get_productsAsync",
      "create_favorAsync",
      "delete_favorAsync"
    ])
  },
  props: {
    s: Object
  },
  created: function() {
    this.set_first(true);
    this.set_count(0);
    let that = this;
    if (!this.products.length) {
      that.getProducts(1, 1, function() {
        that.set_loading(false);
      });
    }
  },
  mounted: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$my-color: #42b983;
$hover-color: #e8f5e9;
$max-width: 1200px;
$text-color: #495057;
.product-box {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: center;
}
.items {
  width: 90%;
  img {
    width: 100%;
    height: auto;
  }
  .ctx-box {
    margin: 10px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1.2rem;
  }
}
img {
  max-width: 1200px;
  width: 100%;
}
.nomore {
  margin-top: 1rem;
}
</style>



