/*
Action 类似于 mutation，不同在于：
Action 提交的是 mutation，而不是直接变更状态。
Action 可以包含任意异步操作。
*/
import HttpReq from '../utils/utils'

const httpReq = new HttpReq()
const actions = {
  async get_userAsync ({
    commit
  }, reqData) {
    await httpReq.getUser(reqData.user, reqData.vm).then(res => {
      if (res.code === 'success') {
        let user = res.user
        console.log(user)
        if (user) {
          commit('get_user', user)
          commit('set_favors', res.favors)
        }
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async get_article_introsAsync ({
    commit
  }, reqData) {
    await httpReq.getArticleIntros(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        commit('get_article_intros', res)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },

  async get_articleAsync ({
    commit
  }, reqData) {
    let JSONdata = {
      req_arr: 'title,id,pic_src,main_html,pv',
      id: reqData.id
    }
    await httpReq.getArticle(JSONdata, reqData.vm).then(res => {
      // payload得到的article是一个长度为1的数组
      if (res.code === 'success') {
        commit('get_article', res.article[0])
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async get_productsAsync ({
    commit
  }, reqData) {
    await httpReq.getProducts(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        commit('get_products', res)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async get_momentsAsync ({
    commit
  }, reqData) {
    await httpReq.getMoments(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        commit('get_moments', res)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async get_getitsAsync ({
    commit
  }, reqData) {
    await httpReq.getGetIts(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        commit('get_getits', res)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_userAsync ({
    commit
  }, reqData) {
    await httpReq.postUser(reqData.vo, reqData.vm).then(res => {
      console.log(res)
      if (res.code === 'success') {
        commit('get_user', res)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_articleAsync ({
    commit
  }, reqData) {
    await httpReq.postArticle(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        commit('get_article', res.result)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_productAsync ({
    commit
  }, reqData) {
    await httpReq.postProduct(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        return res.result
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_momentAsync ({
    commit
  }, reqData) {
    await httpReq.postMoment(reqData.vo, reqData.vm).then(res => {
      if (res.code === 'success') {
        return res.result
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_getitAsync ({
    commit
  }, reqData) {
    await httpReq.postGetIt(reqData.vo, reqData.vm).then(res => {
      console.log(res)
      if (res.code === 'success') {
        console.log(res.result)
        return res.result
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async create_favorAsync ({
    commit
  }, reqData) {
    await httpReq.createFavor(reqData.vo, reqData.vm).then(res => {
      console.log(res)
      if (res.code === 'success') {
        commit('add_favor', res.result)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  },
  async delete_favorAsync ({
    commit
  }, reqData) {
    await httpReq.deleteFavor(reqData.vo, reqData.vm).then(res => {
      console.log(res)
      if (res.code === 'success') {
        commit('remove_favor', res.result)
      } else {
        reqData.vm.$myFunc.toast(res.code)
      }
    })
  }
}
export default actions



