<template>
  <div class="getit flex">
    <div id="search" class="flex">
      <input type="text" v-model="keyword" placeholder="输入标签"><span class="finger" @click="getGetitByTag">搜索</span>
    </div>
    <div class="out-box" v-show="getits.length">
      <div class="tp-box" v-for="get in getits" :key="get.id">
        <TheTags :tags="get.tags"/> 
        <div class="text" v-html="get.text_html"></div>
        <div class="infos flex">
          <div class="author-info flex">
            <div class="avatar" :style="{background:'url('+get.author_avatar+')'}"></div>
            <span class="name">{{get.author_name}}</span>
          </div>
          <button id="get-button" @click="controlLike(get.id)">
            <i :class="[favors.getit.includes(get.id)?'fa-thumbs-up':'fa-thumbs-o-up','fa']"></i>&nbsp;
            {{get.like}}
          </button>
        </div>
      </div>
      <button @click="loadMore" v-if="show_loadmore">加载更多</button>
      <div v-else class="disabled nomore">
        <i class="fa fa-smile-o"></i>&nbsp;
        暂时没有更多啦
      </div>
    </div>
    <div v-show="!getits.length" class="disabled flex-column">
      <img src="../../assets/nomore.png" alt="">

      <span v-if="keyword"><i class="fa fa-frown-o"></i>&nbsp;找不到带该标签的内容哦</span>
      <span v-else>空空如也...</span>
    </div>
  </div>
</template>

<script>
import TheTags from "./TheTags";
import { mapActions, mapMutations, mapGetters } from "vuex";
export default {
  name: "GetIt",
  data() {
    return {
      page: 1,
      limit: 20,
      keyword: ""
    };
  },
  props: ["get"],
  inject: ["reload"],
  components: {
    TheTags
  },
  computed: {
    show_loadmore: function(){
      console.log(this.getits.length,"----",this.count)
      return (this.getits.length!==this.count)
    },
    ...mapGetters(["user", "favors", "getits", "count"])
  },
  methods: {
    controlLike: function(id) {
      if (!this.user) {
        this.check_user(this);
        return;
      }
      let that = this;
      let reqData = {
        vm: this,
        vo: {
          user_id: that.user.id,
          favor_type: "getit",
          favor_id: id
        }
      };
      if (this.favors.getit.includes(id)) {
        // 之前已经点赞，取消赞
        this.delete_favorAsync(reqData).then(() => {
          that.get.like--;
        });
      } else {
        this.create_favorAsync(reqData).then(() => {
          that.get.like++;
        });
      }
    },
    loadMore: function() {
      let scrolltop = this.$myFunc.getScrolltop();
      let that = this;
      that.page++;
      that.getGetit(that.page, 0, function() {
        that.$myFunc.setScrolltop(scrolltop);
        that.set_loading(false);
      });
    },
    getGetit: function(page, get_count, cb, tag = "") {
      let that = this;
      let reqData = {
        vm: that,
        vo: {
          page: page,
          limit: that.limit,
          get_count: get_count,
          tag: tag
        }
      };
      that.set_loading(true);
      that.get_getitsAsync(reqData).then(cb);
    },
    getGetitByTag: function() {
      this.set_first(true);
      this.set_count(0);
      let that = this;
      that.getGetit(
        1,
        1,
        function() {
          that.set_loading(false);
        },
        that.keyword
      );
    },
    ...mapMutations(["check_user", "set_loading", "set_first", "set_count"]),
    ...mapActions(["create_favorAsync", "delete_favorAsync", "get_getitsAsync"])
  },
  created: function() {
    this.set_first(true);
    this.set_count(0);
    let that = this;
    if (!this.getits.length) {
      that.getGetit(1, 1, function() {
        that.set_loading(false);
      });
    }
  }
};
</script>

<style scoped lang="scss">
.getit {
  max-width: 720px;
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
}
#get-button {
  width: 4rem;
  padding: 5px 8px;
}
.out-box {
  width: 100%;
  padding: 0 5px;
  box-sizing: border-box;
}
.tags {
  width: 100%;
}
.tag {
  background: red;
}

.text {
  width: 100%;
  text-align: left;
  padding: 1rem 0;
}

.infos {
  width: 100%;
  justify-content: space-between;
}
.author-info {
  justify-content: flex-start;
}

#get-button {
  padding: 5px 8px;
  margin: 0 1rem;
}
$hover-color: #e8f5e9;
$disable-color: #e0dfcc;
#search {
  width: 100%;
  margin: 1rem auto;
  input {
    height: 2rem;
    line-height: 2rem;
    padding-left: 1rem;
    border-radius: 1rem 0 0 1rem;
  }
  span {
    height: 2rem;
    line-height: 2rem;
    border-radius: 0 1rem 1rem 0;
    padding: 1px 10px;
    font-size: 13px;
    border: 1px solid $disable-color;
    &:hover {
      background: $hover-color;
    }
  }
}
@media screen and (max-width: 500px) {
}
</style>



