
<template>
  <div class="create-getit flex-column">
     <div class="flex attr-input">
      <label for="text">text</label>
      <textarea name="text" type="text" placeholder="text" v-model="text"/>
    </div>

    <CreateTags v-on:send_arr="tags_arr = $event"/>
    
    <button :class="{disabled:not_set}" :disabled="not_set" @click="createGetIt">create Get</button>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapGetters } from "vuex";
import CreateTags from './subcomponents/CreateTags'

export default {
  name: "CreateGetIt",
  data() {
    return {
      text:'',
      tags_arr: [],
    };
  },
  components:{
    CreateTags
  },
  computed: {
    not_set: function() {
      let all_set = this.text.length | this.tags_arr.length;
      return !all_set;
    },
    count_tag: function() {
      return this.tags_arr.length;
    },
    ...mapGetters(["user"])
  },
  methods: {
    mockInit: function() {
      this.tags_arr = [];
      this.text = "";
    },
    createGetIt: function() {
      let that = this;
      let tags_set = new Set(this.tags_arr);
      let reqData = {
        vm: that,
        vo: {
          author: that.user.id,
          text_html: that.text.replaceAll(/\n/g,'<br />'),
          tags: JSON.stringify([...tags_set])
        }
      };
      this.create_getitAsync(reqData).then(() => {
        that.mockInit();
        that.$myFunc.toast("添加成功");
        setTimeout(function(){
          that.$router.push("./discover");
        },900)
      });
    },
    ...mapMutations(['check_user']),
    ...mapActions(["create_getitAsync"])
  },
  created: function() {
    this.check_user(this)
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.create-getit {
  .tag-input {
    width: 100%;
    display: grid;
    grid-template-columns:1fr 6rem;
    .add-tag-btn{
      cursor: pointer;
    }
  }
  #tags-box {
    padding: 5px;
    background: #fff;
    span {
      justify-self: flex-end;
      padding: 0 2rem 0 0;
    }
    .tags {
      max-width: 100%;
      justify-content: flex-start;
    }
  }
}
button {
  margin: 1rem;
}
</style>



