  constructor () {
    // this.host = '111.230.25.202:3000'
    this.host = 'localhost:3000'
  }

  /* --------------------- */
  /* - getUser           - */
  /* --------------------- */
  async getUser (vo, vm) {
    let that = this
    let data
    try {
      await vm.$http.post(`http://${that.host}/api/getuser`, vo, {
        emulate: true
      }).then((res) => {
        console.log(`%c getUser 被调用 `, 'background:#42b983; color:white;')
        data = res.data
      })
      return data
    } catch (err) {
      console.log(err)
      return {
        code: '服务器开小差啦，请稍后再试'
      }
    }
  }

