<template>
  <div id="app">
    <div class="header flex">
      <router-link to="/">Home</router-link>
      <router-link to="/discover">Discover</router-link>
      <router-link to="/archives">Archives</router-link>
      <router-link to="/shop">Shop</router-link>
      <router-link to="/about">About</router-link>
    </div>

    <router-view class="view-box" :s="app_store"/>

    <div class="footer flex small-font">
      <div class="description">
        {{whatsup}}
      </div>
      <span @click="join_favorite"><i :class="[isMarked ? 'fa-bookmark' : 'fa-bookmark-o', 'fa']"></i>  &nbsp;{{markText}}</span>
    </div>

    <div id="cover" v-show="show_loading">
      <i class="fa fa-spinner fa-pulse"></i>
      <span>loading...</span>
    </div>

    <div v-if="user" class="with-user sign finger " :style="{backgroundImage:'url('+user.avatar_url +')'}" @click="goTo(`/user/${user.id}`)"></div>
    <div v-else class="sign flex finger" @click="goTo('/signin')">登录</div>
    <div class="gotop flex finger" v-show="gotop" @click="toTop">
      <i class="fa fa-arrow-circle-up"></i>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "App",
  data() {
    return {
      isMarked: this.$myFunc.getLocal("isMarked"),
      whatsup: "她很忙，她正在死亡",
      gotop: false,
      app_store: {}
    };
  },
  provide:{
    reload: "她很忙，她正在死亡"
  },
  computed: {
    markText: function() {
      return this.isMarked ? "" : "Mark it";
    },
    ...mapGetters(["show_loading", "user"])
  },
  methods: {
    goTo: function(url) {
      this.$router.push(url);
    },
    handleScroll() {
      let scrolltop =
        document.documentElement.scrollTop || document.body.scrollTop;
      scrolltop > 300 ? (this.gotop = true) : (this.gotop = false);
    },
    toTop() {
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      // 实现滚动效果
      const timeTop = setInterval(() => {
        document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
        if (top <= 0) {
          clearInterval(timeTop);
        }
      }, 10);
    },
    join_favorite: async function() {
      let that = this;
      //捕获加入收藏过程中的异常
      let siteUrl = window.location.href;
      let siteName = "greennn's space";

      try {
        //判断浏览器是否支持document.all
        if (window.document.all) {
          //如果支持则用external方式加入收藏夹
          console.log("all");
          window.external.addFavorite(siteUrl, siteName);
          that.$myFunc.setLocal("isMarked", true);
        } else if (window.sidebar) {
          //如果支持window.sidebar，则用下列方式加入收藏夹
          console.log("sidebar");
          window.sidebar.addPanel(siteName, siteUrl, "");
          that.$myFunc.setLocal("isMarked", true);
        } else {
          that.$myFunc.toast(
            "加入收藏夹失败，请使用Ctrl+D快捷键进行添加操作!",
            1000
          );
        }
      } catch (e) {
        //处理异常
        that.$myFunc.toast("加入收藏夹失败，请使用Ctrl+D快捷键进行添加操作!");
      }

      that.$myFunc.setLocal("isMarked", true);
    },
    ...mapMutations(["init"])
  },
  created: function() {
    this.init(this);
  },
  mounted: function() {
    window.addEventListener("scroll", this.handleScroll, true);
  }
};
</script>

<style lang="scss">
@import "./assets/style.scss";
@import "font-awesome/css/font-awesome.css";

#app {
  width: 100%;
  min-height: 100vh;
  max-width: $max-width;
  margin: 0 auto;
  box-sizing: border-box;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 10vh 0 6rem 0;
  background: url("/static/star-bg.1.svg");
  .header {
    max-width: $max-width;
    width: 100%;
    position: fixed;
    top: 0;
    text-align: center;
    background: #fff;
    z-index: 20;
  }
  .footer {
    $footer-color: #868e96;
    position: absolute;
    bottom: 0;
    width: 100%;
    max-width: 1200px;
    background: #fff;
    color: $footer-color;
    // background: url('./assets/foreground.webp');
    .description {
      text-align: center;
      padding: 2rem;
    }
    span {
      .fa {
        cursor: pointer;
      }
      .fa-bookmark {
        color: $my-color;
        &:hover {
          color: $footer-color;
        }
      }
      .fa-bookmark-o {
        &:hover {
          color: $my-color;
        }
      }
    }
  }
  #cover {
    background-image: url("/static/star-bg1.svg");
  }
}
.gotop {
  width: 80px;
  height: 80px;
  z-index: 20;
  position: fixed;
  right: 1rem;
  bottom: 100px;
  border-radius: 50%;
  background: white;
  color: rgba(0, 0, 0, 0.6);
  font-size: 50px;
}
.sign {
  width: 80px;
  height: 80px;
  z-index: 20;
  background-color: #fff;
  border-radius: 50%;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  box-shadow: 0 0 3px 3px $hover-color;
}
.with-user {
  background-size: cover;
  background-position: 50% 50%;
}
@media screen and (max-width: 500px) {
  #app {
    margin: 0;
    padding: 2rem 0 5rem 0;
    .header {
      box-sizing: border-box;
      max-width: 100%;
      flex-wrap: nowrap;
      justify-content: space-between;
      padding: 8px;
      a {
        width: auto;
        padding: 5px 0;
      }
    }
    .view-box {
      box-sizing: border-box;
      width: 100%;
      padding: 10px;
      min-height: 80vh;
    }
  }
  .sign {
    width: 50px;
    height: 50px;
    font-size: 12px;
  }
  .gotop {
    width: 50px;
    height: 50px;
    overflow: hidden;
  }
}
@media screen and (min-width: 1200px) {
  .sign,
  .gotop {
    right: calc(50vw - 600px);
  }
}
</style>



