
<template>
  <div class="signin flex-column">
    <div class="sign-box">
      <img src="../../assets/logo.png" alt="">
      <div v-if="$route.name==='Edit'">You found a secret!</div>
    </div>
    <div class="attr-input">
      <label for="name">username</label>
      <input name="name" type="text" placeholder="username" v-model="myself.name">
    </div>
    <div class="attr-input">
      <label for="psw">password</label>
      <input name="psw" type="password" placeholder="password" v-model="myself.psw" v-on:keyup.enter="signIn">
    </div>
    <div class='remember-psw small-font'><i :class="[icon_class,'fa']" @click="set_remember"></i>&nbsp; 7天内免登录</div>
    <button :class="{disabled:disabled}" :disabled="disabled" @click="signIn">登录</button>
    <div class="to-signup small-font">没有账号？去<a @click.prevent="goTo('/signup')">注册</a></div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import md5 from "js-md5";

export default {
  name: "Signin",
  data() {
    return {
      myself: {
        name: "",
        psw: ""
      },
      init_copy: {
        name: "",
        psw: ""
      }
    };
  },
  computed: {
    icon_class: function(){
      return this.remember_psw? 'fa-toggle-on' : 'fa-toggle-off'
    },
    disabled: function() {
      let all_set = this.myself.name.length>0 & (this.myself.psw.length >= 6);
      return !all_set;
    },
    ...mapGetters(["super_user","user","remember_psw","favors"])
  },
  methods: {
    goTo:function(url){
      this.$router.push(url)
    },
    mockInit: function() {
      this.myself = Object.create(this.init_copy);
    },
    signIn: function() {
      let that = this;
      let userinfo = {};
      userinfo["username"] = this.myself.name;
      userinfo["password"] = md5(this.myself.psw);
      let reqData = {};
      reqData["vm"] = this;
      reqData["user"] = userinfo;
      this.get_userAsync(reqData).then(() => {
        if (that.user) {
          console.log("user....")
          that.$myFunc.setLocal('user',that.user)
          that.$myFunc.setLocal('favors',that.favors)
          that.$myFunc.setLocal('remember_psw',that.remember_psw)
          that.$myFunc.setLocal('logintime',Date.now())
          that.$myFunc.setLocal('user_history',that.user.username)
          that.$myFunc.toast(`hi, ${that.user.username}!`)
          window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
        }
      });
    },
    ...mapMutations(["set_logged","set_remember"]),
    ...mapActions(["get_userAsync"])
  },
  created: function() {
    let user_history = this.$myFunc.getLocal('user_history')
    if(user_history){
      this.myself.name = this.init_copy.name = user_history
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.signin {
  max-width: 100%;
  min-width: 300px;
  background: #fff;
  border-radius: 10px;
  padding: 2rem;
}
.sign-box {
  img {
    width: 150px;
  }
  div {
    font-size: 2rem;
    padding: 1rem;
    color: #42b983;
  }
}
.to-signup{
  padding: 5px;
}
.remember-psw {
  padding: 1rem;
}
</style>



