const getters = {
  count: state => state.count,
  user: state => state.user,
  uv: state => state.uv,
  show_loading: state => state.show_loading,
  pets: state => state.pets,
  article: state => state.article,
  article_intros: state => state.article_intros,
  products: state => state.products,
  moments: state => state.moments,
  getits: state => state.getits,
  favors: state => state.favors,
  remember_psw: state => state.remember_psw,
  super_user: state => state.super_user,
  logged_in: state => state.logged_in
}
export default getters



