<template>
  <div class="create-tags">
    <div class="flex attr-input">
      <label for="tag">tag</label>
      <span class="tag-input">
        <input name="tag" type="text" placeholder="tag" v-model="tag"  v-on:keyup.enter="addTag">
        <div @click="addTag" :class="{disabled:disabled}"  class="add-tag-btn flex"><i class="fa fa-plus-square"></i>&nbsp; Tags</div>
      </span>
    </div>
    <div class="flex attr-input" id="tags-box" v-show="count_tag">
      <span>{{count_tag}} &nbsp;<i class="fa fa-tags"></i></span>
      <div class="tags flex" >
        <div class="tag" v-for="tag in tags_arr" :key="tag" @click="tag_delete = tag">
          {{tag}} &nbsp; 
          <i class="fa fa-close" v-if="tag_delete === tag" @click="deleteTag(tag)"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CreateTags",
  data() {
    return {
      tag_delete: true,
      tag: "",
      tags_arr: []
    };
  },
  computed: {
    count_tag: function() {
      return this.tags_arr.length;
    },
    disabled: function() {
      if (this.tag === "") {
        return true;
      }
      return false;
    }
  },
  methods: {
    addTag: function() {
      if (this.disabled) return;
      this.tags_arr.push(this.tag);
      this.$emit("send_arr", this.tags_arr);
      this.tag = "";
    },
    deleteTag: function(tag) {
      for (let i = 0; i < this.tags_arr.length; i++) {
        if (this.tags_arr[i] === this.tag_delete) {
          this.tags_arr.splice(i, 1);
          this.$emit("send_arr", this.tags_arr);
          break;
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.create-tags {
  width: 100%;
}
.tag-input {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 6rem;
  .add-tag-btn {
    cursor: pointer;
  }
}
#tags-box {
  padding: 5px;
  background: #fff;
  span {
    justify-self: flex-end;
    padding: 0 2rem 0 0;
  }
  .tags {
    max-width: 100%;
    justify-content: flex-start;
  }
}
</style>



