
<template>
  <div class="create-product">
     <div class="flex attr-input" v-for="attr in product_attrs" :key="attr">
       <label :for="attr">{{attr}}</label>
      <input :name="attr" type="text" :placeholder="attr" v-model="product[attr]">
    </div>
    <CreateTags v-if="restart" v-on:send_arr="tags_arr =$event"/>
    <button :class="{disabled:disabled}" :disabled="disabled" @click="createProduct">create product</button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import CreateTags from "./CreateTags";
import { all } from "q";

export default {
  name: "CreateProduct",
  data() {
    return {
      tags_arr: [],
      restart: true,
      product: {
        name: "",
        pic_src: "",
        price: 0,
        like: 0
      },
      init_copy: {
        name: "",
        pic_src: "",
        price: 0,
        like: 0
      }
    };
  },
  components: {
    CreateTags
  },
  computed: {
    product_attrs: function() {
      let product_attrs = [];
      let that = this;
      for (let key in this.product) {
        product_attrs.push(key);
      }
      return product_attrs;
    },
    disabled: function() {
      if (
        this.product.name == undefined ||
        this.product.pic_src == undefined ||
        this.product.price == 0
      ) {
        return true;
      }
      return false;
    }
  },
  methods: {
    mockInit: function() {
      let that = this
      this.tags_arr = [];
      this.product = Object.create(this.init_copy);
      that.$myFunc.toast("添加成功");
        setTimeout(function(){
          that.restart = true
        },100)
    },
    createProduct: function() {
      let that = this;
      let tags_set = new Set(this.tags_arr);
      this.product.tags = JSON.stringify([...tags_set]);
      let reqData = {
        vm: that,
        vo: that.product
      };
      this.create_productAsync(reqData).then(() => {
        that.restart = false
        that.mockInit();
      });
    },
    ...mapActions(["create_productAsync"])
  },
  mounted: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



