
<template>
  <div class="create-article">
     <div class="attr-input" v-for="attr in a_intros_attrs" :key="attr">
       <label :for="attr">{{attr}}</label>
      <input :name="attr" type="text" :placeholder="attr" v-model="a_intros[attr]">
    </div>
    <CreateTags v-if="restart" v-on:send_arr="tags_arr = $event"/>
    <div id="textArea"></div>
    <div class="flex btns">
      <button @click="setIntroHtml">create intro_html</button>
      <button @click="setMainHtml" >create main_html </button>   
      <button :class="{disabled:disabled}" :disabled="disabled" @click="createArticle">create aritcle</button>
    </div>
    <br/>
    intro_html: {{a_intros.intro_html?'ok':'none'}}
    <br/>
    main_html: {{a_intros.main_html?'ok':'none'}}
  </div>
</template>

<script>
import E from "wangeditor";
import { mapGetters, mapActions } from "vuex";
import CreateTags from "./CreateTags";

export default {
  name: "CreateArticle",
  data() {
    return {
      name: "textArea",
      editor: null,
      restart: true,
      tags_arr: [],
      a_intros: {
        title: "",
        pic_src: "",
        intro_html: "",
        main_html: "",
        pv: 0,
        like: 0,
        is_reprint: 0,
        author: ""
      },
      init_copy: {
        title: "",
        pic_src: "",
        intro_html: "",
        main_html: "",
        pv: 0,
        like: 0,
        is_reprint: 0,
        author: ""
      }
    };
  },
  components: {
    CreateTags
  },
  computed: {
    a_intros_attrs: function() {
      let a_intros_attrs = [];
      let that = this;
      for (let key in this.a_intros) {
        if (key === "intro_html" || key === "main_html") {
          continue;
        }
        a_intros_attrs.push(key);
      }
      return a_intros_attrs;
    },
    disabled: function() {
      if (
        this.a_intros.title == 0 ||
        this.a_intros.pic_src == 0 ||
        this.a_intros.intro_html == 0 ||
        this.a_intros.main_html == 0
      ) {
        return true;
      }
      return false;
    },
    ...mapGetters(["user"])
  },
  methods: {
    mockInit: function() {
      let that = this;
      this.tags_arr = [];
      this.a_intros = Object.create(this.init_copy);
      this.editor.txt.editor.$textElem[0].innerHTML = "";
      that.$myFunc.toast("添加成功");
      setTimeout(function() {
        that.restart = true;
      }, 100);
    },
    createArticle: function() {
      let that = this;
      let tags_set = new Set(this.tags_arr);
      this.a_intros.tags = JSON.stringify([...tags_set]);
      this.a_intros.author_id = this.user.id;
      let reqData = {
        vm: that,
        vo: that.a_intros
      };
      this.create_articleAsync(reqData).then(function() {
        that.restart = false;
        that.mockInit();
      });
    },
    getHtml: function() {
      let t = document.getElementById(this.name);
      // 返回富文本框中输入内容的html
      this.inputHtml = this.editor.txt.editor.$textElem[0].innerHTML;
      return this.inputHtml;
    },
    setIntroHtml: function() {
      this.a_intros.intro_html = this.getHtml();
    },
    setMainHtml: function() {
      this.a_intros.main_html = this.getHtml();
    },

    ...mapActions(["create_articleAsync", "create_productAsync"])
  },
  mounted: function() {
    this.editor = new E(`#textArea`);
    this.editor.create();
    let that = this;
    console.log(this.user);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.btns {
  button {
    margin: 1rem;
  }
}
#textArea {
  width: 100%;
  .w-e-toolbar {
    flex-wrap: wrap !important;
  }
}
</style>



