<template>
  <div class="article" v-if="article">
    <h1>{{article.title}}</h1>
    <div class="infos flex">
      <span>
        <i :class="[{liked:favors.article.includes(article.id)},'fa','fa-heart','finger']" @click="controlLike(article.id)"></i> &nbsp;{{article.like}} &nbsp;
        <i class="fa fa-eye"></i>  &nbsp;{{article.pv}}
      </span>
      <span class="author">
        <span class="small-font" v-if="article.is_reprin">转自 </span>&nbsp;
        <i class="fa fa-pencil-square-o"></i>
        <span>{{article.author}}</span>
      </span>
    </div>
    <img :src="article.pic_src" alt="main pic">
    <div class="content-part" v-html="article.main_html"></div>
    <TheTags :tags="article.tags"/>
    <div class="hr"></div>
    <div class="comment disabled">
      <i class="fa fa-gears"></i>
      <span> {{comment}} </span> 
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import TheTags from "./subcomponents/TheTags";
export default {
  name: "Article",
  data() {
    return {
      comment: '评论功能即将开放...'
    };
  },
  components: {
    TheTags
  },
  computed: {
    ...mapGetters(["article","user","favors"])
  },
  methods: {
    controlLike: function(id){
      if(!this.user){
        this.check_user(this)
        return
      }
      let that = this
      let reqData = {
        vm: this,
        vo: {
          user_id: that.user.id,
          favor_type: 'article',
          favor_id: id
        }
      }
      if(this.favors.article.includes(id)){
        // 之前已经点赞，取消赞
        this.delete_favorAsync(reqData).then(()=>{
          that.article.like --
        })
      } else {
        this.create_favorAsync(reqData).then(()=>{
          that.article.like ++
        })
      }
    },
    ...mapMutations(["set_loading","check_user"]),
    ...mapActions(["get_articleAsync",'create_favorAsync','delete_favorAsync'])
  },
  props: {
    s: Object,
  },
  created: function() {
    let id = this.$route.params.id;
    let that = this;
    if (this.article) {
      if (that.article.id == id) {
        return;
      }
    }
    let reqData = {};
    reqData["vm"] = this;
    reqData["id"] = id;

    this.set_loading(true);
    this.get_articleAsync(reqData).then(() => {
      that.set_loading(false);
      // console.log(that.article);
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.article {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
  box-sizing: border-box;
  background-color: #fff;
  .infos {
    justify-content: space-around;
    margin-bottom: 10px;
  }
  img {
    max-width: 100% !important;
  }
  .content-part {
    text-align: left;
  }
  .comment {
    margin: 1rem;
  }
}
</style>



