<template>
  <div class="user flex-column" id="user">
    <div class="hi text">你好， {{user.username}}</div>
    <div class="text">该页面建设中...</div>
    <img src="../assets/building.png" alt="">
    <div class="describe">
      <label for="list">将会添加以下功能：</label>
      <ul name="list">
        <li>查看您发表的Getit</li>
        <li>查看你的喜欢及收藏</li>
        <li>查看您对文章的评论建议</li>
      </ul>
    </div>
    <button @click="goBack">返回</button>
    <button @click="logOut">登出</button>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import Infos from "./subcomponents/Infos";
import TheTags from "./subcomponents/TheTags";
import Signup from "./subcomponents/Signup";
import CreateTags from "./subcomponents/CreateTags";
import Signin from "./subcomponents/Signin";

export default {
  name: "User",
  data() {
    return {};
  },
  components: {
    Infos,
    TheTags,
    Signup,
    CreateTags,
    Signin
  },
  computed: {
    ...mapGetters(["logged_in", "user"])
  },
  methods: {
    logOut(){
      let that = this
      this.goBack()
      setTimeout(function(){
        that.log_out(that)
      },300)
    },
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    ...mapMutations(["check_user","log_out"])
  },
  props: ["s"],
  created: function() {
    this.check_user(this);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#user{
  button{
    padding: 5px 1rem;
    margin: 1rem;
  }
}
.hi {
  font-size: 2rem;
  margin-bottom: 20px;
}
.text {
  color: #42b983;
}
label {
  font-size: 13px;
}
ul {
  text-align: left;
  font-size: 13px;
  padding-inline-start: 20px;
  li {
    // list-style: none;
  }
}
img {
  width: 50%;
  max-width: 256px;
}
</style>



