<template>
  <div class="moments">
    <div class="moment-boxes">
      <div class="moment-box flex-column" v-for="(item,index) in moments" :key="index">
        <div class='text' v-html="item.text_html"></div>
        <div class='pic-box' v-if ="item.pics.length">
          <div v-for="pic in item.pics" :key="pic" :style="{backgroundImage:'url('+pic+')'}" @click="showPic(pic)">
            <img src="../assets/trans.png">
          </div>
        </div>
        <div class="text time">{{item.created_at | formatTime}}</div>
      </div>
    </div>
    <div id="pic_detail" class="pic-detail flex" v-show="show_pic" @click="closePic($event)">
      <img :src="pic_src" alt="pic details">
    </div>
    <!-- <div class="photos-boxes">
      <div class="photos-box flex-column" v-for="(item,index) in photos_list" :key="index">
        <img :src="item.cover" alt="cover-picture">
        <div class="title">{{item.title}}</div>
        <div class="tags">
          <div class="tag" v-for="tag in item.tags" :key="tag">
            {{tag}}
          </div>
        </div>
      </div>
    </div> -->
    <button @click="loadMore" v-if="moments.length!==count">加载更多</button>
    <div v-else class="disabled nomore">
      <i class="fa fa-smile-o"></i>&nbsp;
      暂时没有更多啦
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "Momnets",
  data() {
    return {
      page: 1,
      limit: 20,
      show_pic: false,
      pic_src: "",
      photos_list: [
        {
          title: "富士山·201701",
          cover: "https://img.alicdn.com/bao/uploaded/i4/2206795693295/O1CN01aW27Rf1aD9cmU8EYn_!!2206795693295.png_120x120.jpg",
          tags: ["japan"]
        },{
          title: "富士山·201701",
          cover: "https://img.alicdn.com/bao/uploaded/i4/2206795693295/O1CN01aW27Rf1aD9cmU8EYn_!!2206795693295.png_120x120.jpg",
          tags: ["japan"]
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["show_loading","moments",,"count"])
  },
  methods: {
    showPic: function(pic){
      this.pic_src = pic
      this.show_pic = true
    },
    closePic: function(e){
      let that = this
      if(e.target.id === 'pic_detail') {
        that.pic_src = ''
        that.show_pic = false
      }
    },
    loadMore: function() {
      let scrolltop = this.$myFunc.getScrolltop()
      let that = this
      that.page++;
      let reqData = {
        vm: that,
        vo: {
          page: that.page,
          limit: that.limit,
          get_count: 0
        }
      };
      this.set_loading(true);
      that.get_momentsAsync(reqData).then(function() {
        that.$myFunc.setScrolltop(scrolltop)
        that.set_loading(false);
      });
    },
    ...mapMutations(["set_loading","set_first","set_count"]),
    ...mapActions(["get_momentsAsync"])
  },
  created: function(){
    this.set_first(true)
    this.set_count(0)
    let that = this
    if (!this.moments.length) {
      let reqData = {
        vm: that,
        vo: {
          page: 1,
          limit: that.limit,
          get_count: 1
        }
      };
      that.set_loading(true)
      that.get_momentsAsync(reqData).then(function(){
        that.set_loading(false)
      })
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.moments {
  justify-content: space-around;
}
.moment-boxes {
  :last-child{
    border: none;
  }
}
.moment-box {
  width: 60%;
  min-width: 320px;
  margin: 0 auto;
  padding: 5px 1rem;
  border-bottom: 1px solid #E8F5E9;
}
.text {
  padding-top: 1rem;
  width: 100%;
  word-break: break-all;
  text-align: left;
}

.pic-detail{
  width: 100vw;
  height: 100vh;
  z-index: 99;
  background: rgba(0,0,0,0.7);
  position: fixed;
  top:0;
  left:0;

  img {
    max-width: 800px;
    min-width: 300px;
    width:100%;
  }
}
.pic-box {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fill,100px);
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  margin-top: 1rem;
  img {
    width: 100%;
  }
  div {
    background-position: 50% 50%;
    background-size: cover;
  }
}
</style>



