
<template>
  <div class="create-moment">
     <div class="flex attr-input">
      <label for="text">text</label>
      <textarea name="text" type="text" placeholder="text" v-model="moment.text"/>
    </div>
    <div class="flex attr-input">
      <label for="pic_src">pic_src</label>
      <input name="pic_src" type="text" placeholder="pic_src" v-model="moment.pic_src">
    </div>
    <button @click="addPic"><i class="fa fa-plus-square"></i>&nbsp; pictrues: {{count_pic}}</button>
    <button :class="{disabled:not_set}" :disabled="not_set" @click="createMoment">create aritcle</button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { all } from "q";

export default {
  name: "CreateMoment",
  data() {
    return {
      moment: {
        text: "",
        pic_src: ""
      },
      pics_arr: [],
      init_copy:{
        text: "",
        pic_src: ""
      }
    };
  },
  computed: {
    not_set: function() {
      let all_set = this.moment.text.length | this.pics_arr.length;
      return !all_set;
    },
    count_pic: function() {
      return this.pics_arr.length;
    }

  },
  methods: {
    mockInit: function(){
      this.pics_arr = []
      this.moment = this.init_copy
    },
    addPic:function(){
      this.pics_arr.push(this.moment.pic_src)
      this.moment.pic_src = ''
    },
    createMoment: function() {
      let that = this;
      let reqData = {
        vm: that,
        vo: {
          text_html: that.moment.text.replaceAll(/\n/g,'<br />'),
          pics: JSON.stringify(that.pics_arr)
        }
      };
      this.create_momentAsync(reqData).then(()=>{
        that.mockInit();
        that.$myFunc.toast('添加成功');
      })
    },
    ...mapActions(["create_momentAsync"])
  },
  mounted: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
button {
  margin: 1rem;
}
</style>



