<template>
  <div class="signup flex-column" id="signup">
    <div class="sign-box">
      <img src="../../assets/logo.png" alt="">
      <div>Hi! My new friend!</div>
    </div>
    <div class="attr-input">
      <label for="name">username</label>
      <input name="name" type="text" placeholder="username" v-model="new_user.name">
    </div>
    <div class="attr-input">
      <label for="psw1">password</label>
      <input name="psw1" type="password" placeholder="password" v-model="new_user.psw1">
    </div>
    <div class="attr-input">
      <label for="psw2">password</label>
      <input name="psw2" type="password" placeholder="password again" v-model="new_user.psw2">
    </div>
    <div class="choose-avatar flex">
      <img class="avatar" :src="selected_avatar" alt="avatar">
      <button class="" @click="chooseAvatar">{{btn_text}}</button>
    </div>
    <div class="avatar-box" v-show='btn_text === "ok"'>
      <img :class="[item===selected_avatar?'selected':'','avatar','finger']" v-for="item in avatars" :key="item" :src="item"  @click="setSelected(item)" alt="avatar">
    </div>
    <div class="error-box small-font" v-show="error"> <i class="fa fa-warning"></i>&nbsp; {{error}}</div>
    <button :class="{disabled:!checked_ok}" :disabled="!checked_ok" @click="signUp">注册</button>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import md5 from "js-md5";

export default {
  name: "Signup",
  data() {
    return {
      error: "",
      selected_avatar: require("../../assets/avatars/monkey.png"),
      btn_text: "更换头像",
      new_user: {
        name: "",
        psw1: ""
      },
      init_copy: {
        name: "",
        psw1: "",
        psw2: ""
      },
      avatars: [
        'http://imgc.taro.ai/2017-03/2646BEE9-B5BA-46B6-97E6-DE153AB13C38/pano_b.jpg',
        'https://gw.alicdn.com/bao/uploaded/i1/O1CN01m6oeMc1PAtw7zdKZd_!!0-fleamarket.jpg_570x10000Q75.jpg_.webp',
        'https://lc-gold-cdn.xitu.io/47d583f976f9bcb861c3?imageView2/0/w/1280/h/960/format/webp/ignore-error/1',
        'https://lc-gold-cdn.xitu.io/e9a75262b7f88dc97ff3?imageView2/0/w/1280/h/960/format/webp/ignore-error/1',
        'https://t9.baidu.com/it/u=1775900009,2371222529&fm=79&app=86&size=h300&n=0&g=4n&f=jpeg?sec=1601262706&t=677a94c6f0e597ab7b548f7ffabe9e12',
        require("../../assets/avatars/monkey.png"),
        require("../../assets/avatars/bear.png"),
        require("../../assets/avatars/bee.png"),
        require("../../assets/avatars/bird.png"),
        require("../../assets/avatars/cat.png"),
        require("../../assets/avatars/cow.png"),
        require("../../assets/avatars/dog.png"),
        require("../../assets/avatars/donkey.png"),
        require("../../assets/avatars/duck.png"),
        require("../../assets/avatars/elephant.png"),
        require("../../assets/avatars/fox.png"),
        require("../../assets/avatars/frog.png"),
        require("../../assets/avatars/giraffe.png"),
        require("../../assets/avatars/hippo.png"),
        require("../../assets/avatars/koala.png"),
        require("../../assets/avatars/lion.png"),
        require("../../assets/avatars/mouse.png"),
        require("../../assets/avatars/owl.png"),
        require("../../assets/avatars/panda.png"),
        require("../../assets/avatars/penguin.png"),
        require("../../assets/avatars/pig.png"),
        require("../../assets/avatars/rabbit.png"),
        require("../../assets/avatars/sheep.png"),
        require("../../assets/avatars/tiger.png")
      ]
    };
  },
  computed: {
    checked_ok: function() {
      let that = this;
      that.error = "";
      if (
        this.new_user.name == undefined ||
        this.new_user.psw1 == undefined ||
        this.new_user.psw2 == undefined
      ) {
        return false;
      }
      if (this.new_user.psw1.length < 6 || this.new_user.psw2.length < 6) {
        that.error = "密码至少6位数";
        return false;
      }
      if (this.new_user.psw1 !== this.new_user.psw2) {
        that.error = "两次密码不一致";
        return false;
      }
      return true;
    },
    ...mapGetters(["user"])
  },
  methods: {
    mockInit: function() {
      this.error = "";
      this.new_user = Object.create(this.init_copy);
    },
    setSelected: function(id) {
      this.selected_avatar = id;
    },
    chooseAvatar: function() {
      if (this.btn_text === "ok") {
        this.btn_text = "更换头像";
      } else {
        this.btn_text = "ok";
      }
    },
    signUp: function() {
      let that = this;
      let userinfo = {};
      userinfo["username"] = this.new_user.name;
      userinfo["password"] = md5(this.new_user.psw1);
      userinfo["avatar_url"] = this.selected_avatar
      let reqData = {};
      reqData["vm"] = this;
      reqData["vo"] = userinfo;
      this.create_userAsync(reqData).then(() => {
        console.log(that.user);
        if (that.user.username === that.new_user.name) {
          that.$myFunc.setLocal("user", that.user);
          that.$myFunc.setLocal("logintime", Date.now());
          that.$myFunc.setLocal("user_history", that.user.username);
          that.$myFunc.toast("注册成功！", 1000, "即将跳转登录...");
          that.mockInit();
          setTimeout(function() {
            window.history.length > 1
              ? that.$router.go(-2)
              : that.$router.push("/");
          }, 1000);
        }
      });
    },
    ...mapMutations(["set_logged"]),
    ...mapActions(["create_userAsync"])
  },
  mounted: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.signup {
  min-width: 300px;
  max-width: 500px;
  margin: 0 auto;
  background: #fff;
  border-radius: 10px;
  padding: 2rem;
}
.sign-box {
  img {
    width: 100px;
  }
  div {
    font-size: 2rem;
    padding: 1rem;
    color: #42b983;
  }
}
#signup {
  .avatar {
    width: 60px;
    height: 60px;
    margin: 0;
  }
  .choose-avatar {
    width: 100%;
    margin-bottom: 1rem;
    button {
      width: fit-content;
      min-width: 4rem;
      padding: 5px 8px;
      margin: 0 1rem;
    }
  }

  .avatar-box {
    width: 90%;
    margin-bottom: 1rem;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(auto-fill, 64px);
    grid-row-gap: 10px;
    grid-column-gap: 10px;
    justify-content: center;
    justify-items: center;
    align-items: center;
    padding: 1rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 3px 3px #E8F5E9;
  }
  .selected {
    border-radius: 10px;
    border: 2px solid #42b983;
  }
  button{
    margin: 1rem;
  }
}
</style>



