
<template>
  <div class="edit">
    <div class="flex btns" v-if="super_user">
      <div :class="[{selected: isComp2},'comp-tab']" @click="currentTabComponent=comp2">{{comp2}}</div>
      <div :class="[{selected: isComp3},'comp-tab']" @click="currentTabComponent=comp3">{{comp3}}</div>
      <div :class="[{selected: isComp4},'comp-tab']" @click="currentTabComponent=comp4">{{comp4}}</div>
    </div>
    <component :is="currentTabComponent" v-if="super_user"></component>
    <div class="cover" v-if="logged_in&!super_user">
      <div class="no-power flex-column">
        <img src="../assets/logo.png" alt="">
        <div>You found a secret!</div>
        <span>没有权限</span>
        <span>去别处逛逛吧！</span>
      </div>
    </div>
    
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import CreateProduct from "./subcomponents/CreateProduct";
import CreateArticle from "./subcomponents/CreateArticle";
import CreateMoment from "./subcomponents/CreateMoment";
import Signin from "./subcomponents/Signin";

export default {
  name: "Edit",
  data() {
    return {
      currentTabComponent: "CreateProduct",
      comp1: "Signin",
      comp2: "CreateProduct",
      comp3: "CreateArticle",
      comp4: "CreateMoment"
    };
  },
  components: {
    CreateArticle,
    CreateProduct,
    CreateMoment,
    Signin
  },
  computed: {
    isComp2: function() {
      return this.currentTabComponent === this.comp2 ? true : false;
    },
    isComp3: function() {
      return this.currentTabComponent === this.comp3 ? true : false;
    },
    isComp4: function() {
      return this.currentTabComponent === this.comp4 ? true : false;
    },
    ...mapGetters(["logged_in", "super_user"])
  },
  methods: {
    ...mapMutations(["set_logged","check_user"])
  },
  created: function() {
    this.check_user(this)
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.edit {
  .comp-tab {
    padding: 1rem 2rem;
    margin: 2rem;
    background: #eee;
    border-radius: 10px;
  }
  .selected {
    background: #e8f5e9;
  }
  .cover {
    z-index: 10;
  }
}
.no-power {
  box-sizing: border-box;
  max-width: 320px;
  background: #fff;
  padding: 1rem;
  border-radius: 10px;
  color: #42b983;
  img {
    width: 150px;
  }
  div {
    font-size: 2rem;
    padding: 1rem;
  }
  span {
    padding: 1rem;
  }
}
@media screen and (max-width: 500px) {
  .edit {
    .comp-tab {
      width: 50%;
      padding: 1rem 2rem;
      margin: 0.5rem 2rem;
    }
  }
}
</style>



