/*
更改 Vuex 的 store 中的状态的唯一方法是提交 mutation
*/
const mutations = {
  init: (state, vm) => {
    let user = vm.$myFunc.getLocal('user')
    let lasttime = vm.$myFunc.getLocal('logintime')
    let favors = vm.$myFunc.getLocal('favors')
    if (user) {
      if (vm.$myFunc.getLocal('remember_psw')) {
        // 7天内免登录
        if (Date.now() - lasttime > 604800000) {
          return
        }
      } else {
        // 未设置7天免登录，则3小时内有效
        if (Date.now() - lasttime > 10800000) {
          return
        } else {
          vm.$myFunc.setLocal('user', null)
          return
        }
      }
      state.user = user
      state.super_user = user.super_user
      state.favors = favors
      state.logged_in = true
      console.log('set user ok')
    }
  },
  increment: state => {
    state.count += 1
  },
  get_user: (state, user) => {
    state.user = user
    state.super_user = user.super_user
    state.logged_in = true
    console.log(user)
  },
  log_out: (state, vm) => {
    state.user = null
    state.super_user = false
    state.logged_in = false
    vm.$myFunc.setLocal('user', null)
    vm.$myFunc.setLocal('favors', null)
  },
  check_user: (state, vm) => {
    if (state.logged_in) {
      return
    }
    vm.$myFunc.toast('请先登录')
    setTimeout(() => {
      vm.$router.push('./signin')
    }, 800)
  },
  set_logged: (state, tf) => {
    state.logged_in = tf
  },
  set_favors: (state, favors) => {
    state.favors = favors
  },
  set_remember: (state) => {
    state.remember_psw = !state.remember_psw
  },
  set_super_user: (state, tf) => {
    state.super_user = tf
  },
  set_loading: (state, tf) => {
    state.show_loading = tf
  },
  set_count: (state, count) => {
    state.count = count
  },
  set_first: (state, tf) => {
    state.first_page = tf
  },
  get_article: (state, article) => {
    article.tags = JSON.parse(article.tags)
    state.article = article
  },
  get_article_intros: (state, payload) => {
    if (state.first_page) {
      //  第一次请求，直接设置结果(带有总条数count)
      state.article_intros = payload.article_intros
      state.count = payload.count
      state.first_page = false
    } else {
      // 非第一次请求，拼接结果
      let arr = state.article_intros.concat(payload.article_intros)
      state.article_intros = arr
    }
  },
  get_products: (state, payload) => {
    if (state.first_page) {
      //  第一次请求，直接设置结果(带有总条数count)
      state.products = payload.products
      state.count = payload.count
      state.first_page = false
    } else {
      // 非第一次请求，拼接结果
      let arr = state.products.concat(payload.products)
      state.products = arr
    }
  },
  get_moments: (state, payload) => {
    let moments = payload.moments
    moments.forEach(e => {
      e.pics = JSON.parse(e.pics)
    })
    if (state.first_page) {
      //  第一次请求，直接设置结果(带有总条数count)
      state.moments = moments
      state.count = payload.count
      state.first_page = false
    } else {
      // 非第一次请求，拼接结果
      let arr = state.moments.concat(moments)
      state.moments = arr
    }
    state.moments = moments
  },
  get_getits: (state, payload) => {
    let getits = payload.getits
    getits.forEach(e => {
      e.tags = JSON.parse(e.tags)
    })
    if (state.first_page) {
      //  第一次请求，直接设置结果(带有总条数count)
      state.getits = getits
      state.count = payload.count
      state.first_page = false
    } else {
      // 非第一次请求，拼接结果
      let arr = state.getits.concat(getits)
      state.getits = arr
    }
  },
  add_favor: (state, payload) => {
    // payload为一个两位的数组，第一位为点赞目标的类型，第二位为目标id
    state.favors[payload[0]].push(payload[1])
    console.log(state.favors)
    localStorage.setItem('favors', JSON.stringify(state.favors))
  },
  remove_favor: (state, payload) => {
    // payload为一个两位的数组，第一位为点赞目标的类型，第二位为目标id
    let arr = state.favors[payload[0]]
    arr.splice(arr.findIndex(e => e === payload[1]), 1)
    console.log(state.favors)
    localStorage.setItem('favors', JSON.stringify(state.favors))
  }
}

export default mutations



